/* Typography */
h1, h2 {
  margin: 2rem 2rem 20rem 2rem; }

p {
  margin: 1rem; }

/* Template segments */
header {
  display: block;
  width: 100vw;
  min-height: 5rem;
  background-color: #445577;
  position: fixed;
  top: 0;
  left: 0;
  padding: 10px 15px; }
  header a#logo {
    color: #ededed;
    font-weight: 1000; }

footer {
  display: block;
  width: 100vw;
  min-height: 5rem;
  background-color: #445577;
  color: #ededed;
  position: fixed;
  bottom: 0;
  left: 0; }

.main {
  max-width: 74rem;
  margin: 8rem auto 0 auto; }

.navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; }
  .navigation__item {
    width: 180px;
    text-align: center; }
